.modal {
  position: absolute;
  z-index: 10000;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.7);
  color: $text-color-main;

  &__mode {
    color: $selected-color;
    text-align: right;
    margin-bottom: 10px;
    opacity: 0.7;

    &:hover {
      color: $accent-color;
    }
  }

  &__form {
    display: grid;
    width: 350px;
    margin: 10% auto;
    padding: 25px;
    background-color: $bg-color-main;
    border: 1px solid $bg-color-controllers;
    border-radius: 4px;
  }

  &__input {
    display: grid;
    margin-bottom: 20px;

    & label {
      margin: 5px;
    }

    & input {
      padding: 15px;
      border: 1px solid $bg-color-controllers;
      border-radius: 4px;
    }
  }

  &__action {
    margin-top: 10px;
    padding: 15px;
    background-color: $accent-color;
    color: $text-color-accent-btn;
    border-radius: 4px;

    &:hover {
      background-color: $accent-color-hov;
    }
  }

  &__close {
    position: relative;
    top: -20px;
    right: -20px;
    justify-self: end;
    width: 32px;
    height: 32px;
    border-radius: 4px;

    & svg {
      margin: auto;
    }

    &:hover svg {
      fill: $selected-color;
    }
  }
}
